// // Video Player
// // --------------------------------------------------
//
// // Module color variable
// @controls-color: mix(@brand-primary, black, 75%);
//
.uams-player {
  margin-bottom: 80px  ;

  .video-js {

    background-color: transparent;
    // Otherwise you won't see controls in Fullscreen mode

    // margin: 20px 10px 80px 10px;
    position: relative;
    padding: 0;
    // font-size: 10px;
    // vertical-align: middle;

    -webkit-backface-visibility: hidden;
    -moz-backface-visibility:hidden;
    -ms-backface-visibility:hidden;
    backface-visibility:hidden;

    .vjs-tech {
      // position: absolute;
      // top: 0;
      // left: 0;
      width: 100%;
      // height: 100%;
    }

    &.vjs-fullscreen {
      position: fixed;
      overflow: hidden;
      z-index: 1000;
      left: 0;
      top: 0;
      bottom: 0;
      right: 0;
      width: 100% !important;
      height: 100% !important;
      _position: absolute;
    }

    .vjs-play-control,
    .vjs-current-time,
    .vjs-duration,
    .vjs-remaining-time,
    .vjs-live-controls,
    .vjs-progress-control,
    .vjs-fullscreen-control,
    .vjs-volume-control,
    .vjs-mute-control,
    .vjs-playback-rate,
    .vjs-subtitles-button,
    .vjs-captions-button,
    .vjs-chapters-button {
      // display:none;
      // outline: thin solid red;
      text-indent: -1000px;
    }

    .vjs-playback-rate,
    .vjs-subtitles-button,
    .vjs-captions-button,
    .vjs-chapters-button {
      display:none;
    }

    .vjs-control-bar {
      margin-top: -10px;
      position: relative;
      // bottom: -50px;
      // left: 0;
      // right: 0;
      // margin: 0;
      // padding: 0;
      // height: 50px;
      color:gray;
      background: #4d4d4d;
      width:100%;
      height: 50px;

      // Play button
      .vjs-play-control {
        // display:block;
        position: absolute;
        left: 10px;
        height:100%;
        width: 50px;
        background: url(assets/svg/uams-sprite.svg) no-repeat -350px -100px;
        text-indent:-1000px;
        .transition( all .25s )
      }


      // Progress Control
      .vjs-progress-control {
        // display:block;
        position: absolute;
        left: 80px;
        right: 180px;
        height: 12px;
        width: 57%;
        top: 18px;
        background: lightgray;
        border-radius: 32px;

        .vjs-progress-holder {

          height:100%;

          .vjs-load-progress {
            background: white;
            height: 100%;
            // display:none;
          }

          // Progress handle
          .vjs-seek-handle {
            background-color: @50grey;
            position: absolute;
            width: 18px;
            height: 18px;
            margin: -3px 0 0 1px;
            left: 0;
            top: 0;
            border-radius: 50%;
            .transition(background-color .25s);

            &[style*='95.'] {
              margin-left: 3px;
            }
            &[style='left: 0%;'] {
              margin-left: -2px;
            }
            &:hover,
            &:focus {
              background-color: lighten( @50grey, 10%);
            }
            &:active {
              background-color: lighten( @50grey, 10%);
            }
          }

        }

      }

      .vjs-fullscreen-control {
          position:absolute;
          top:0;
          right:0;
          width:50px;
          height:100%;
      }

      .vjs-live-controls {
        display:none;
      }

      .vjs-volume-control {
        position: absolute;
        right: 50px;
        top: 0;
        width: 50px;
        height: 100%;
      }

      // Player time controls
      // Current time
      .vjs-current-time {
        color: white;
        text-indent: 0px;
        text-align: center;
        font-size: @font-size-base;
        left: 70%;
        span {
          display:none;
        }

      }

      // Duration
      .vjs-duration {
        text-indent:0px;
        text-align: center;
        font-size: @font-size-base;
        left: 78%;
        span {
          display:none;
        }
      }

      // Remaining time
      .vjs-remaining-time {
        display: none;
      }

      // Diverder between current and duration
      .vjs-time-divider {
        color: gray;
        font-size: @font-size-base;
        position: absolute;
        top: 11px;
        left: 77%;
      }

      //todo: what is this for?
      &.vjs-fade-out {
        visibility: visible !important;
        opacity: 1 !important;
      }

    }

  // Video player control general style


    .vjs-time-controls {
      position: absolute;
      height: 20px;
      width: 50px;
      top: 16px;
      font: 300 13px @font-family-base;
    }



    &:-webkit-full-screen {
      width: 100% !important;
      height: 100% !important;
    }
    &:-moz-full-screen {
      position: absolute;
    }



  }

  body.vjs-full-window {
    padding: 0;
    margin: 0;
    height: 100%;
    overflow-y: auto;
  }

}

